
import { defineComponent } from "vue";
import { useRoute } from "vue-router";
import { useProductSingle } from "@/hooks/useProductSingle";
import ProductOptions from "@/components/ProductOptions.vue";
import ProductCartBlock from "@/components/ProductCartBlock.vue";

export default defineComponent({
  components: {
    ProductOptions,
    ProductCartBlock,
  },
  setup() {
    const route = useRoute();
    const productId = +route.params.id;
    const { product, isLoadingProduct } = useProductSingle(productId);

    const optionsList = [
      { title: "", value: product.value.category },
      { title: "", value: product.value.size },
      { title: "", value: product.value.color },
      { title: " ", value: product.value.quantity },
    ];

    return {
      product,
      isLoadingProduct,
      optionsList,
    };
  },
});
